import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Offcanvas from "react-bootstrap/Offcanvas";
import LogoBisnis from "../assets/images/Chamytwinslogo.png";
import { useLocation } from "react-router-dom";
import React, { useEffect, useState } from "react";

function OffcanvasExample() {
  let loc = useLocation();

  const [scrolled, setScrolled] = useState(false);

  const handleScroll = () => {
    setScrolled(window.scrollY > 50);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);
  return (
    <>
      {[false].map((expand) => (
        <Navbar
          key={expand}
          expand={expand}
          className={`${
            scrolled
              ? "con-nav-scroll position-fixed"
              : "con-nav position-fixed"
          }`}
        >
          <Container fluid>
            <Navbar.Brand
              href="/ChamyTwins-Website"
              className="position-absolute top-50 end-0 translate-middle-y custom-brand-nav"
            >
              <div className="img1-con-nav position-relative overflow-hidden">
                <img
                  className="logobisnis object-fit-contain"
                  alt="chamytwinslogo"
                  src={LogoBisnis}
                />
              </div>
            </Navbar.Brand>
            <Navbar.Toggle
              aria-controls={`offcanvasNavbar-expand-${expand}`}
              className={
                loc.pathname === "/"
                  ? scrolled
                    ? "position-absolute top-50 start-0 translate-middle-y custom-icon border-none"
                    : "position-absolute top-50 start-0 translate-middle-y custom-icon-hmpg border-none"
                  : "position-absolute top-50 start-0 translate-middle-y custom-icon border-none"
              }
            />
            <Navbar.Offcanvas
              id={`offcanvasNavbar-expand-${expand}`}
              aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
              placement="start"
            >
              <Offcanvas.Header closeButton className="nav-item">
                <Offcanvas.Title
                  id={`offcanvasNavbarLabel-expand-${expand}`}
                  className="custom-nav-title"
                >
                  ChamyTwins
                </Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Nav className="justify-content-end flex-grow-1 pe-3 nav-item">
                  <Nav.Link href="/ChamyTwins-Website">Homepage</Nav.Link>
                  <Nav.Link href="#/caresheet">Chamelon Caresheet</Nav.Link>
                  <Nav.Link href="#/chamytwinsMember">
                    ChamyTwins Family
                  </Nav.Link>
                </Nav>
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </Container>
        </Navbar>
      ))}
    </>
  );
}

export default OffcanvasExample;
